/<template>
  <div class="container">
    <div class="case1" v-if="step == 0">
      <button class="newpost" @click="next">새글등록하기</button>
      <PostA v-for="(a, i) in data" :key="i" :data="data[i]" />
    </div>
    <div class="case2" v-if="step == 1">
      <div class="create">
        <div class="nameW">
          강아지 이름<span>8글자내로 입력가능합니다.</span
          ><input type="text" class="nameI" @input="nameM" maxlength="8" />
        </div>
        <div class="ageW">
          나이<span>숫자만 입력가능합니다.</span>
          <input type="number" class="ageI" @input="ageM" />
        </div>
        <div class="breedW">
          품종<span>ex: 시베리아허스키, 푸들등</span>
          <input type="text" class="breedI" @input="breedM" />
        </div>
        <div class="characteristicW">
          특징<span>ex: 귀가 서있다, 가슴에 반점이있다등</span>
          <input type="text" class="characteristicI" @input="characteristicM" />
        </div>
        <div class="nicknameW">
          닉네임 <span>8글자내로 입력가능합니다</span>
          <input
            type="text"
            maxlength="8"
            class="nicknameI"
            @input="nicknameM"
          />
        </div>
        <div class="PhotoW">
          사진등록
          <input type="file" multiple accept="image/*" @change="photoM" />
          <div class="prePhoto">
            <div
              v-for="a in photoC"
              :key="a"
              :style="{ backgroundImage: `url(${a})` }"
            ></div>
          </div>
        </div>
        <button @click="doneC">등록하기</button>
      </div>
    </div>
  </div>
  <!--class명 뒤에 붙는 대문자 W: 입력 div들 / M: wirte Methods function / I: write Input 란-->
</template>

<script>
import axios from 'axios';
import PostA from "./post.vue";
export default {
  name: "ContainA",
  data() {
    return {
      step: 0,
      /* photoC - 사용자가 photo에 입력한 데이터란 뜻 */
      nameC: "",
      ageC: "",
      breedC: "",
      characteristicC: "",
      nicknameC: "",
      photoC: "",
      newData: "",
    };
  },
  components: {
    PostA,
    /* PostA 게시물  */
  },
  props: {
    data: Array,
  },
  methods: {
    next(){
        this.step ++
    },
    nameM(e) {
      this.nameC = e.target.value;
    },
    ageM(e) {
      this.ageC = e.target.value;
    },
    breedM(e) {
      this.breedC = e.target.value;
    },
    characteristicM(e) {
      this.characteristicC = e.target.value;
    },
    nicknameM(e) {
      this.nicknameC = e.target.value;
    },
    photoM(e) {
      let file = e.target.files;
      let fileLength = file.length;
      let photoList = [];

      for (let i = 0; i < fileLength; i++) {
        photoList.push(URL.createObjectURL(file[i]));
      }
      this.photoC = photoList;
      console.log(this.photoC[0]);
    },

    doneC() {
      this.newData = {
        no: 0,
        name: this.nameC,
        age: this.ageC,
        breed: this.breedC,
        characteristic: this.characteristicC,
        likes: 0,
        nickname: this.nicknameC,
        photo: this.photoC,
      };
      this.data.unshift(this.newData)
      this.step = 0;
      console.log(this.newData);
    },
  },
};
</script>

