/<template>
  <div class="post">
    <div class="front">
      <div class="picture"  :style="{ backgroundImage : `url(${data.photo[0]})` }"></div>
      <div class="information">
        <h4>이름 : {{ data.name }}</h4>
        <p>나이 : {{ data.age }} 살</p>
        <p>견종 : {{ data.breed }}</p>
        <p>특징 : {{ data.characteristic }}</p>
        <p>좋아요 {{ data.likes }} 회</p>
        <p>작성자 : {{ data.nickname }}</p>
        <!-- <button>자세히 보기</button> -->
        <button @click="More">사진 더 보기</button>
      </div>
    </div>
    <div class="back" v-if="morePic">
      <div class="morePicture" v-for="(a,i) in data.photo" :key="i" 
      :style="{ backgroundImage : `url(${data.photo[i]})` }"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "postA",
  props: {
    data: Object,
  },
  data() {
    return {
      morePic: false,
    };
  },
  methods: {
    More(e) {
      if (this.morePic == false) {
        this.morePic = true;
        e.target.innerHTML = "사진 접기";
      } else {
        this.morePic = false;
        e.target.innerHTML = "사진 더 보기";
      }
    },
  },
};
</script>

